.aside {
  overflow: hidden;
}

.aside__wrapper {
  padding: 0 20px;


  @media (min-width: $tablet-width) {
    padding: 0;
  }

  @media (min-width: $desktop-width) {
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
  }
}

.aside__title {
  margin: 0;
  margin-bottom: 30px;

  font-family: $main-font;
  font-size: 24px;
  line-height: 1;
  font-weight: 400;
  color: #111111;

  @media (max-width: $only-mobile) {
    line-height: 30px;
  }

  @media (min-width: $tablet-width) {
    margin-bottom: 79px;

    font-size: 36px;
    line-height: 1;
  }

  @media (min-width: $desktop-width) {
    width: 100%;
    padding: 0;
  }

  span {
    display: inline-block;

    padding-right: 22px;
    background-color: #ffffff;

    @media (min-width: $tablet-width) {
      padding-left: 35px;
      padding-right: 35px;
    }

    @media (min-width: $desktop-width) {
      margin-left: -30px;
      padding-left: 30px;
    }
  }
}

.aside__list {
  @include list-reset;
  margin-bottom: 39px;

  @media (min-width: $tablet-width) {
    margin-bottom: 69px;
  }

  @media (min-width: $desktop-width) {
    width: 895px;
    margin: 0 80px 0 0;
  }
}

.aside__item {
  padding: 13px 0 16.5px;

  border-top: 1px solid #ededed;

  @media (min-width: $tablet-width) {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: baseline;
    padding: 26px 38px;

    border-right: 1px solid #ededed;
    border-left: 1px solid #ededed;
  }

  @media (min-width: $desktop-width) {
    padding: 16px 0 14px;

    border-left: none;
    border-right: none;
  }

  &:last-child {
    border-bottom: 1px solid #ededed;
  }
}

.aside__title-item {
  flex-shrink: 0;
  margin: 0;
  margin-bottom: 15px;

  font-family: $main-font;
  font-size: 16px;
  line-height: 1;
  font-weight: 400;
  text-align: left;
  text-transform: uppercase;
  color: #222222;

  @media (min-width: $tablet-width) {
    max-width: 50%;
    margin-bottom: 0;

    font-size: 20px;
  }

  @media (min-width: $desktop-width) {
    width: 25%;
    margin-right: 100px;
  }
}

.aside__inner {
  display: flex;
  justify-content: space-between;
  margin-bottom: 12px;

  @media (min-width: $tablet-width) {
    justify-content: flex-end;
    width: 50%;
    margin-bottom: 0;
    margin-left: auto;
  }

  @media (min-width: $desktop-width) {
    width: 289px;
    justify-content: flex-start;
    margin: 0;
  }
}

.aside__quantity {
  font-family: $second-font;
  font-size: 14px;
  line-height: 1;
  text-align: left;
  color: #444444;

  @media (min-width: $tablet-width) {
    font-size: 16px;
    line-height: 1.25;
    text-align: right;
  }

  @media (min-width: $desktop-width) {
    text-align: left;
  }
}

.aside__quantity--amount {
  max-width: 70%;

  @media (min-width: $tablet-width) {
    margin-right: 52px;
  }

  @media (min-width: $desktop-width) {
    margin-right: auto;
  }
}

.aside__btn {
  font-size: 16px;
  line-height: 1.25;

  @media (min-width: $tablet-width) {
    margin-top: 21px;
    padding-bottom: 10px;
  }

  @media (min-width: $desktop-width) {
    width: 245px;
    margin: 0;
    margin-left: auto;
  }
}

.aside__inner-gift {
  padding: 65px 10px 39px;

  background-image: url("../img/gift-photo-mobile@1x.jpg");
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-color: #65a93c;

  @media (min-resolution: $retina-dpi) {
    background-image: url("../img/gift-photo-mobile@2x.jpg");
    background-size: 100% auto;
  }

  @media (min-width: $tablet-width) {
    padding: 74px 0 69px;

    background-image: url("../img/gift-photo-tablet@1x.jpg");
    background-position: 0 0;

    @media (min-resolution: $retina-dpi) {
      background-image: url("../img/gift-photo-tablet@2x.jpg");
      background-size: 708px auto;
    }
  }

  @media (min-width: $desktop-width) {
    display: block;
    width: 245px;
    padding: 65px 35px 44px;

    background-image: url("../img/gift-photo-desktop@1x.jpg");
    bacgkround-size: cover;

    @media (min-resolution: $retina-dpi) {
      background-image: url("../img/gift-photo-desktop@2x.jpg");
      background-size: cover;
    }
  }
}


.aside__gift {
  position: relative;
  margin: 0;
  padding: 125px 40px 0;

  font-family: $second-font;
  font-size: 16px;
  line-height: 1.25;
  color: #ffffff;
  text-align: center;

  @media (min-width: $tablet-width) {
    padding: 0;
    padding-left: 425px;
    padding-right: 120px;

    text-align: left;
  }

  @media (min-width: $desktop-width) {
    width: 100%;
    padding: 125px 0 0 0;

    text-align: center;
  }

  &::after {
    position: absolute;
    content: "";
    top: -10px;
    left: 50%;
    margin-left: -28px;
    width: 56px;
    height: 61px;

    background-image: url("../img/svg/icon-gift.svg");
    background-repeat: no-repeat;
    background-position: 0 0;

    @media (min-width: $tablet-width) {
      left: 90px;
      margin: 0;
    }

    @media (min-width: $desktop-width) {
      top: 0;
      left: 50%;
      margin-left: -28px;
    }
  }
}
